.counterValues {
  max-width: 515px;
  padding: 10px;
  margin: 0 auto;
  box-sizing: border-box;
}

.counterValuesInnerWrap {
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  min-height: 131px;
  padding: 18px 15px 10px 15px;
  border: 1px solid #fff;
}

.counterValuesInnerWrap>div {
  width: 50%;
  padding: 10px 14px;
}

.counterValuesCol button {
  width: 100%;
  margin: 8px 0 0 0;
}

@media (max-width: 450px) {
  .counterValuesInnerWrap {
    min-height: 1px;
    padding: 10px 10px 15px 10px;
  }

  .counterValuesInnerWrap>div {
    padding: 5px 10px;
  }
}